.home{
    background: #F0F0F0;
    width: 100%;
    height: 750px;
    min-width: 1000px;
}
.home .form{
    min-width:915px;
}
.home .pro-content{
    width: 80%;
    margin: 0 auto;
}
.home  .pro-nav {
    padding: 20px 0;

}
.home .pro-nav img{
    height: 60px;
}
.home .pro-center{
    background: white;
    width: 100%;
    padding-top: 40px;
    height: 750px;
    padding-left: 10%;
    min-width: 1000px;
}
.home .pro-prompt p{
    font-size: 1.5rem;
    color:#4288C5;
    font-weight:600;
}
.home .pro-prompt p span img{
    display: inline-block;
    vertical-align: middle;
    width: 1.5rem;
    height: 1.5rem;
    margin-right: 0.5%;
    margin-bottom: 3px;
}
.home .pro-prompt div{
    color: #C2C2C2;
    line-height: 35px;
}
.home .form .checkBox{
    line-height: 45px;
    font-size: 1.2rem;
}
.home .form .checkBox input{
    width: 1rem;
    height: 1rem;
}
.home .form .checkBox  .check{
    display: inline-block;
width: 30%;
}
.home .form textarea{
    width: 70%;
    height: 100px;
    resize: none;
    padding: 10px;
    border:2px solid #F1F1F1;
    font-size: 1rem;

    /*color: #C2C2C2;*/
}
.home .form h4{
    line-height: 40px;
    font-weight: bold;
}
.home .form h4 span{
    color: #C2C2C2;
}
.home .form .contactWay label{
    line-height: 45px;
}
.home .form .contactWay label input{
    border: 1px solid #C2C2C2;
    width: 26%;
    height: 30px;
    padding: 5px;
}

.home .form .contactWay .phoneNumber{
    padding-right:5%;
}
.home .form .formSubmit{
padding-left: 25%;
}
.home .form .formSubmit input{
    background:#3A5A95 ;
    color: white;
    text-align: center;
    width: 30%;
    height: 30px;
    border: none;
    margin: 40px auto;
}
.home .form{
    position: relative;
}
.home .form div.twoCode{
    position: absolute;
    right:10%;
    bottom:120px;
}





